* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
#search-box:focus {
    outline: none;
    border:2px solid #00BFFF;
}
.country-list {
  list-style: none;
  display: grid;
  gap: 15px;
  padding-left: 0;
}
.country-info {
  font-weight: 500;
  font-size: 22px;
}

span {
  font-size: 24px;
  font-weight: 500;
}